<template>
  <div id="export">
    <mt-header title="" fixed>
      <mt-button  icon="back" slot="left" @click="handleBack()"></mt-button>
      <!-- <mt-button  slot="right" @click="gotoPostAsk()"></mt-button> -->
    </mt-header>
    <div class="lise_40"></div>

    <section class="userWarp" v-if="expert">
      <div class="avatar">
        <img :src="getUserAvatar" />
      </div>
      <h6>{{expert.trueName}}</h6>
      <span class="btn" @click="follow()" v-if="!getIsFollow"></span>
      <span class="btn selectd" @click="follow()" v-if="getIsFollow"></span>
      <div class="label"><i v-for="item in getLabel">{{item}}</i></div>
      <p>{{expert.signContent}}</p>
      <span class="twBtn" @click.stop.prevent="gotoZJTW()" v-if="userID != expert.id">TA</span>
    </section>

    <!--  Warp -->
    <div id="report_Warp">
      <div class="header_title"><h2 style="margin-left:10px;">TA</h2></div>
      <!-- ******  ****** -->
      <li v-for="item in askArr" >
        <router-link :to="{path: '/askDetail', query:{qid: item.q_id}}">
          <MyAnswerList :itemData="item" :type="2"></MyAnswerList>
        </router-link>
      </li>
    </div>


    <!-- <div class="noData" v-if="list.length == 0"> 0 </div>
    <div @click="seeing">
      <div class="loading-start" v-if="loading"></div>
      <span class="more" v-if="!complete && !loading"></span>
    </div> -->

  </div>
</template>

<script>
import { Toast,Indicator,Loadmore,DatetimePicker} from 'mint-ui'
import {getStore, setStore,imgBaseUrl} from '../../components/common/mUtils'
import ExpertCell from '../home/children/expertCell'
import MyAnswerList from '../myinfo/children/MyAnswerList'
import $ from 'webpack-zepto'
export default {
  components:{
    ExpertCell,
    MyAnswerList
  },
  data(){
    return{
      imgBaseUrl,
      userID:'',
      userToken:'',
      isLogin:false,
      eid:0,
      expert:null,
      askArr:[],
      complete: false, // 
      loading: false, // 
      curPage:1, // 
      pageSize:10, // 10
      loading:false,
      complete:true,
    }
  },
  created(){
    this.eid = this.$route.query.eid;
    this.userID = getStore('userID')
    this.userToken = getStore('userToken')
    // console.log(this.userID)
    if (this.userID) {
      this.isLogin = true
    }
  },
  mounted(){
    // 
    this.getExpert()

    // 
    this.getAskArr()
  },
  filters:{
  },
  computed:{
    getUserAvatar:function(){
      if(this.expert){
        if (this.expert.avatarPath == '') {
          return "http://jimg.07358.com/Fnhc3IKMv02O5Udciy3gsPCf6C6y"
        }else {
          let indexOf = this.expert.avatarPath.indexOf('http://tu.07358.com')
          if(indexOf != -1){
            return this.expert.avatarPath
          }else {
            return imgBaseUrl + this.expert.avatarPath
          }
        }
      }else {
        return "http://jimg.07358.com/Fnhc3IKMv02O5Udciy3gsPCf6C6y"
      }
    },
    getLabel(){
      let imags = this.expert.tags.split(',')
      return imags;
    },
    getIsFollow(){
      if (this.isLogin) {
        return this.expert.cangzj.length > 0 ? true : false;
      }else {
        return false
      }
    }
  },
  methods: {
    gotoZJTW(){
      if (this.isLogin) {
        this.$router.push({path: '/publishAsk', query:{zjid:this.expert.id}})
      }else {
        this.$router.push('/login')
      }
    },
    handleBack(){
      this.$router.go(-1)
    },
    gotoAddress(path){
      this.$router.push(path)
    },
    gotoPostAsk(){
      this.userID = getStore('userID')
      if (this.userID) {
        this.$router.push('/publishAsk')
      }else {
        this.$router.push('/login')
      }
    },
    // 
    getAskArr(){
      let urlStr = ''
      if (this.isLogin) {
        urlStr = '&uid='+this.userID
      }
      let getUrl = this.CFWConfig.APIHost + '/v3/ask/zjdetail?id='+this.eid+urlStr
      // console.log(getUrl)
      this.$http.get(getUrl).then(response => {
        let code = response.data.code
        if (code == 200) {
          this.expert = response.data.result
        }
        Indicator.close();
      }, response => {
        Toast('');
        console.log(response);
      });
    },
    // 
    getExpert(){
      let getUrl = this.CFWConfig.APIHost + '/v3/ask/zjanswer?id='+this.eid
      // console.log(getUrl)
      this.$http.get(getUrl).then(response => {
        let code = response.data.code
        if (code == 200) {
          this.askArr = response.data.result
        }
        Indicator.close();
      }, response => {
        Toast('');
        console.log(response);
      });
    },
    follow(){
      if (!this.validateLogin()) {
        return
      }

      if (this.userID == this.expert.id) {
        Toast('');
        return
      }

      Indicator.open('...');
      let param = {
        'itemid':this.expert.id,
      }
      // let getUrl = this.CFWConfig.APIHost + '/v3/ask/dian?token='+this.userToken
      let getUrl = this.CFWConfig.APIHost + '/v3/ask/gzzj?token='+this.userToken
      this.axios.post(getUrl,this.objToformData(param))
      .then(function (response) {
        let code = response.data.code
        if (code == 200) {
          //this.getIsFollow()
          this.getAskArr()
        }
        Toast(response.data.message);
        Indicator.close();
      }.bind(this))
      .catch(function (error) {
        console.log(error);
      });
    },
    validateLogin(){
      if (!this.isLogin) {
        MessageBox.confirm(',').then(({ action }) => {
          this.$router.push('/login')
        }, response => {
        });
        return false
      }else {
        return true
      }
    },
    // 
    objToformData(obj){
      // let params = new URLSearchParams()
      let params = new FormData()
      Object.keys(obj).forEach( (key, i) => {
        params.append(key, obj[key]);
      });
      return params;
    },
  },
}
</script>

<style>
/**/
@import '../../style/common/style.css';

#export{

}

#export .userWarp{
  /*height: 200px;*/
  padding: 20px 0px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-bottom: 1px solid #f6f6f6;
}

#export .userWarp .avatar{
  width: 90px;
  height: 90px;
  border: 1px solid #e8e8e8;
  border-radius: 45px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

#export .userWarp .avatar img{
  width: 90%;
  height: 90%;
  border-radius: 45px;
}

#export .userWarp .label{
  padding: 10px 0px;
}

#export .userWarp h6{
  margin: 10px 0px 0px;
  font-size: 15px;
  line-height: 20px;
  font-weight: bold;
}

#export .userWarp .btn{
  margin: 10px 0px;
  border: 1px solid #31a4fc;
  background-color: #31a4fc;
  font-size: 13px;
  padding: 5px 15px;
  border-radius: 2px;
  color: #fff;
  font-weight: bold;
}
#export .userWarp .twBtn{
  /*margin-right: 20px;*/
  border: 1px solid #fdb738;
  background-color: #fdb738;
  font-size: 13px;
  padding:5px 15px;
  border-radius: 2px;
  color: #fff;
  font-weight: bold;
  min-width: 60px;
}

#export .userWarp .selectd{
  border: 1px solid #ddd;
  background-color: #ddd;

}

#export .userWarp .label i{
  padding: 3px 12px;
  font-style: normal;
  font-size: 13px;
  background-color: rgb(228, 246, 238);
  color: rgb(0, 138, 119);
  margin-right: 10px;
  margin-left: 10px;
  border-radius: 12px;
}

#export .userWarp p{
  margin: 10px 20px;
  font-size: 13px;
  line-height: 16px;
}

</style>



